import { ref, watch, defineComponent, computed } from 'vue';
import { ocrCompatible } from '@/utils/compatibility';
import { getScannerInstance } from '../scanner/scanner.worker';
import 'vue-virtual-scroller/dist/vue-virtual-scroller.css';
import CustomElScrollVue from '@/components/ElCustomScroll.vue';
import { DynamicScroller, DynamicScrollerItem } from 'vue-virtual-scroller/src/index';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faInbox } from '@fortawesome/free-solid-svg-icons';
import { vLoading } from 'element-plus/es/components/loading/src/directive';
import 'element-plus/theme-chalk/el-loading.css';
import fastq from 'fastq';
import { useRoute } from 'vue-router';
import { IMatFromImageElement } from '@/utils/IMat';
import FloatContent from '../CaptureScanner/FloatContent.vue';
import { send } from '../utils';
library.add(faInbox);
export default defineComponent({
  components: {
    FloatContent,
    DynamicScroller,
    DynamicScrollerItem
  },
  directives: {
    elloading: vLoading
  },

  setup() {
    const load = ref(false);
    const loading = ref(false);
    const step = ref(1);

    if (!ocrCompatible) {
      return {
        load,
        step: 0,
        recognized: {
          success: 0,
          fail: 0
        },
        images: []
      };
    }

    const fileInput = ref(null);
    const images = ref([]);
    const dragOver = ref(false);

    const dropzoneClick = () => {
      if (fileInput.value) {
        fileInput.value.click();
      }
    };

    const onFileDrop = e => {
      if (!e.dataTransfer) return;
      dragOver.value = false;
      const files = e.dataTransfer.files;
      onFiles(files);
    };

    const onFileChange = e => {
      const files = e.target.files;
      if (!files) return;
      onFiles(files);
    };

    const onFiles = async files => {
      // make files as <img> objects in base64
      images.value = [];
      loading.value = true;
      const imagePromises = Array.from(files).map((file, index) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        return new Promise(resolve => {
          reader.onload = () => {
            const img = new Image();
            img.src = reader.result;
            img.id = 'rcycle-img-' + index;

            img.onload = () => {
              resolve(img);
            };
          };
        });
      });
      images.value = await Promise.all(imagePromises);
      loading.value = false;
    };

    const results = ref([]);
    const dup = ref(0);
    const recognized = computed(() => {
      return {
        success: results.value.filter(r => r.success).length,
        fail: results.value.filter(r => !r.success).length
      };
    });
    const progress = computed(() => {
      return (recognized.value.success + recognized.value.fail + dup.value) / images.value.length * 100;
    });
    watch([images, recognized, dup], () => {
      send('progress', {
        scanned: images.value.length,
        ...recognized.value,
        dup: dup.value
      });
    });
    const route = useRoute();

    const recoginzeWorker = async ({
      image,
      thread
    }) => {
      let s = scannerOnLine;

      if (thread) {
        s = scannerOnLine2;
      }

      const imat = await IMatFromImageElement(image);
      const r = await s(imat); // 检查重复

      if (r.success) {
        const r2 = results.value.find(i => {
          if (!i.success) return false;
          const n = i;
          return n.achievement.id === r.achievement.id;
        });

        if (!r.done || route.query.withImage) {
          r.images = {
            main: image.src
          };
        }

        if (r2) {
          dup.value++;
        } else {
          results.value.push(r);
        }
      } else {
        r.images = {
          main: image.src
        };
        results.value.push(r);
        new Image().src = r.images.main;
      }
    };

    const ocrQueue = fastq.promise(recoginzeWorker, 2);

    async function startScan() {
      ocrQueue.killAndDrain();
      ocrQueue.pause();
      let last = Promise.resolve();
      images.value.forEach((element, index) => {
        last = ocrQueue.push({
          image: element,
          thread: index % 2 === 0
        });
      });
      ocrQueue.resume();
      await last;
      send('state', 'finish');
      send('result', {
        result: results.value,
        dup: dup.value,
        metadata: {
          ua: navigator.userAgent,
          scanner: 'LineScanner'
        }
      });
    }

    watch(step, v => {
      if (v === 2) {
        startScan();
      }
    });
    const {
      scannerOnLine,
      scannerOnLine2
    } = getScannerInstance();
    return {
      step,
      load,
      loading,
      onFileDrop,
      onFileChange,
      dropzoneClick,
      fileInput,
      dragOver,
      CustomElScrollVue,
      images,
      progress,
      recognized,
      results,
      dup
    };
  }

});